import React from "react";

const Logo = () => {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="60"
      height="60"
      viewBox="0 0 1598 1500"
    >
      <image
        id="Background"
        width="60"
        height="60"
        xlinkHref="data:img/png;base64,"
      />
      <image
        id="Layer_1_copy"
        data-name="Layer 1 copy"
        width="1598"
        height="1500"
        xlinkHref="data:img/png;base64,"
      />

      <image
        x="150"
        y="192"
        width="1500"
        height="1500"
       xlinkHref="data:img/png;base64,"
      />
    </svg>
  );
};
export default Logo;
