import AdapterDateFns from "@mui/lab/AdapterDateFns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import MobileDateTimePicker from "@mui/lab/MobileDateTimePicker";
import { Avatar, Button, TextField } from "@mui/material";
import React, { useState } from "react";
import { styled } from "@mui/system";
import { ClickAwayListener } from "@mui/base";
import AxiosMain from "../../../API/AxiosMain";
import AddIcon from "@mui/icons-material/Add";
import AddLinkIcon from "@mui/icons-material/AddLink";
import { useEffect } from "react";
import Attachment from "../Attachment";
import AddLink from "@mui/icons-material/AddLink";

const CssTextField = styled(TextField)({
  "& label.Mui-focused": {
    color: "#f36f21",
  },
  "& .MuiInput-underline:after": {
    borderBottomColor: "#f36f21",
  },
  "& .MuiOutlinedInput-root": {
    "& fieldset": {
      borderColor: "black",
    },
    "&:hover fieldset": {
      borderColor: "#f36f21",
    },
    "&.Mui-focused fieldset": {
      borderColor: "#f36f21",
    },
  },
});

const MakeDeadline = () => {
  const [LoginData, setLoginData] = useState(
    localStorage.getItem("loginData")
      ? JSON.parse(localStorage.getItem("loginData"))
      : null
  );
  const [Coordinates, setCoordinates] = useState([]);

  // make deadline
  const [Owner, setOwner] = useState([]);
  const [Content, setContent] = useState("");
  const [Student, setStudent] = useState([]);
  const [attachment, setattachment] = useState("");
  const [DateValue, setDateValue] = useState(Date());
  const [Note, setNote] = useState("");
  const [Link, setLink] = useState("");

console.log(`onthign`,attachment)

  const handlePost = async () => {
    if (Owner && Content && Student && Attachment && Note) {
      await AxiosMain.post("/api/deadline/create", {
        "userId": `${LoginData.user._id}`,
        "owner": [],
       "student": [],
        "content": `${Content}`,
        "attachment":[attachment],
       "note": `${Note}`,
        "deadlinedate": Date.parse(DateValue),
      });
    }
  };

  return (
    <>
      <div className="student-quick-add-deadline">
        <TextField
          id="standard-basic"
          label="YOUR DEADLINE NAME"
          variant="standard"
          color="warning"
          value={Content}
          onChange={(newValue) => {
            setContent(newValue.target.value);
          }}
          style={{
            marginTop: "14px",
            marginRight: "12px",
            width: "100%",
            bordercolor: "#f36f21",
          }}
        />
          <div style={{display: "flex", alignItems: "flex-end"}}>
        <TextField
          id="standard-basic"
          label="NOTE"
          variant="standard"
          color="warning"
          value={Note}
          onChange={(newValue) => {
            setNote(newValue.target.value);
          }}
          style={{
            marginRight: "12px",
            transition: "linear 0.2s",
            width: "100%",
            bordercolor: "#f36f21",
          }}
        /> 
        <AddLink color="warning"></AddLink>
        </div>
        <div className="emberlink-block">
        <input className="emberlink" type="text" />
        </div>

        { attachment && <Attachment data={[attachment]} />}

        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <MobileDateTimePicker
            renderInput={(props) => (
              <CssTextField
                fullWidth
                size="small"
                {...props}
                sx={{
                  svg: { color: "#f36f21" },
                }}
                style={{
                  marginTop: "15px",
                  color: "#f36f21",
                  zIndex: 0,
                }}
              />
            )}
            inputFormat="dd/MM/yyyy hh:mm a"
            label="DUE"
            value={DateValue}
            minDate={new Date()}
            onChange={(newValue) => {
              setDateValue(newValue)
              
            }}
          />
        </LocalizationProvider>
        <div style={{fontSize: 18, fontWeight:600, textTransform:"capitalize" , margin: " 5px 0"}}>more option</div>
      </div>
      <div className="deadline-popup-avatar-and-btn">
        <div className="deadline-popup-group-avt">
          <div className="deadline-popup-group-avt group-avt">
            <Avatar
              src="data:image/png;base64,"
              sx={{ width: 24, height: 24, marginRight: "5px" }}
            />
          </div>
          <div className="deadline-popup-add-people-btn">
            <AddIcon style={{ color: "#f36f21" }}></AddIcon>

            <div className="deadline-popup-add-people-btn-title">
              Add More Receive
            </div>
          </div>
        </div>

        <Button
          color="warning"
          variant="contained"
          style={{ height: "40px", transform: "translateY(40px)" }}
          onClick={handlePost}
        >
          Make Deadline
        </Button>
      </div>
    </>
  );
};

export default MakeDeadline;
